.container{
    width: 1100px;
    height: 500px;
    margin: 20px auto 0 auto;
    position: relative;
    border-radius: 8px;
    box-shadow: -20px 25px 20px #ddd;
}
.title{
    display: inline-block;
    position: absolute;
    top: 50px; right: 300px;
}
.coverImg{
    position: absolute;
    top: 80px; left: 100px;
    box-shadow: -20px 15px 30px #ddd;
}
.UlList{
    list-style: none;
    display: flex;
    position: absolute;
    top: 115px; right: 50px;
}
.List{
    padding: 0 10px;
    font-weight: bold;
}