.container{
    width: 1100px;
    height: 300px;
    position: relative;
    margin: 20px auto;
    border-radius: 10px;
    box-shadow: -23px 18px 18px #ddd;
}
.reset{
    text-decoration: none;
    color: cornflowerblue;
}
.reset:hover{
    text-decoration: underline;
    cursor: pointer;
}
.title{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    text-align: center;
    padding-top: 20px;
}
.container2{
    width: 80%;
    height: 300px;
    float: right;
}
.container3{
    width: 20%;
    height: 300px;
    position: relative;
}
.sizing{
    width: 100%;
    height: 270px;
    padding: 0 0 10px 20px;
    box-shadow: 5px 7px 30px #edd;
}
.text{
    padding: 30px 50px;
}
.ulList{
    background-color: rgba(0, 0, 0,0.5);
    color: #ddd;
    list-style: none;
    width: 220px;
    height: 270px;
    position: absolute;
    top: 0; left: 20px;
    margin: 0;
    opacity: 0;
    padding-left: 0;
}
.ulList:hover{
    opacity: 1;
}
.List{
    position: relative;
    top: 100px;
    text-align: center;
}
.List:hover{
    cursor: pointer;

}